import '../App.css';
import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col} from 'react-bootstrap';
import Portallist from './portallist';
import Assetchart from './assetchart';
import Portalchart from './portalchart';
import Mychart from './mychart'
import Testchart from './testchart'
import chartdata from '../data/chart.json';



function Topincident() {
    return(
        <div>
            <Container>
                <Row>
                    <Col xxl="4"> </Col>
                    <Col xxl="8">  </Col>
                </Row>
                <Row>
                    <Col xxl="4"><Assetchart /></Col>
                    <Col xxl="8">
                        <Container fluid="xxl">
                        <Row>
                            <Col xxl="6"><Portalchart data={chartdata[0]}/></Col>
                            <Col xxl="6"><Portalchart data={chartdata[1]}/></Col>
                        </Row>
                        <Row>
                            <Col xxl="6"><Portalchart data={chartdata[2]}/></Col>
                            <Col xxl="6"><Portalchart data={chartdata[3]}/></Col>
                        </Row>
                        </Container>
                    </Col>
                </Row>
                <Row>
                    <Col xxl="4"><Testchart i="0" /></Col>
                    <Col xxl="4"><Testchart i="1" /></Col>
                    <Col xxl="4"><Testchart i="2" /></Col>
                </Row>
                <Row>
                    <Col xxl="4"><Testchart i="3" /></Col>
                    <Col xxl="4"><Testchart i="4" /></Col>
                    <Col xxl="4"><Testchart i="5" /></Col>
                </Row>
                <Row>
                    <Col xxl="4"><Testchart i="6" /></Col>
                    <Col xxl="4"><Testchart i="7" /></Col>
                    <Col xxl="4"><Testchart i="8" /></Col>
                </Row>
            </Container>

        </div>
    )
};

export default Topincident;
